/* tslint:disable:no-console */
import React from 'react'
import { ScrollView } from 'react-native'
import { List, TextareaItem, Toast } from '../../'

export default class BasicTextAreaItemExample extends React.Component<
  any,
  any
> {
  constructor(props: any) {
    super(props)
    this.state = {
      val: 'value',
    }
  }

  onChange = (val: any) => {
    // console.log(val);
    this.setState({ val })
  }

  render() {
    return (
      <ScrollView
        style={{ flex: 1 }}
        automaticallyAdjustContentInsets={false}
        showsHorizontalScrollIndicator={false}
        showsVerticalScrollIndicator={false}>
        <List renderHeader={''}>
          <TextareaItem rows={4} placeholder="" />

          <TextareaItem rows={4} placeholder="" count={100} />

          <TextareaItem
            rows={4}
            placeholder=""
            autoHeight
            style={{ paddingVertical: 5 }}
          />

          <TextareaItem value={this.state.val} onChange={this.onChange} />

          <TextareaItem value=" editable = {false}" editable={false} />

          <TextareaItem clear={false} placeholder="" />

          <TextareaItem
            error
            defaultValue=" error={true}"
            onErrorClick={() => Toast.fail('Error message')}
          />
        </List>
      </ScrollView>
    )
  }
}
